*,
::before,
::after {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	outline: none;
	border: 0;
}

body {
	margin: 0;
	font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif !important;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.575), rgba(0, 0, 0, 0.575)), url(/bikeform/static/media/bikes.6269114f.jpg) !important;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	height: 100vh;
}

body:before {
	content: '';
	display: block;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: -10;
	background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.863), rgba(0, 0, 0, 0.692)), url(/bikeform/static/media/bikes.6269114f.jpg) !important;
	background-size: cover;
}

.container {
	max-width: 1200px !important;
	background-color: rgba(255, 255, 255, 0.151);
	box-shadow: 0px 4px 12px 0px #000000;
	transform: translateY(10vh);
	color: white;
}

select {
	/* width: 260px !important; */
	background-color: rgba(0, 0, 0, 0.6) !important;
	font-weight: bolder !important;
	-webkit-appearance: none !important;
	appearance: none !important;
	background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E') !important;
	background-repeat: no-repeat, repeat !important;
	background-position: right 0.7em top 50%, 0 0 !important;
	background-size: 0.65em auto, 100% !important;
}

input[type='number'] {
	background-color: rgba(0, 0, 0, 0.6) !important;
}

.myBtn {
	font-weight: 300;
	color: rgba(255, 255, 255, 0.294) !important;
	background-color: rgba(0, 0, 0, 0.575) !important;
}

.btn-plus:hover {
	color: rgb(255, 255, 255) !important;
	background-color: rgba(9, 119, 27, 0.5) !important;
}

.myBtn.btn-plus {
	width: 60px;
	height: 60px;
	border-radius: 35px;
	transition: all 350ms ease;
}

.myBtn.btn-minus {
	transition: all 350ms ease;
}

.btn-minus:hover {
	color: rgb(255, 255, 255) !important;
	background-color: rgba(179, 1, 1, 0.466) !important;
}

.myBoldHeading {
	font-family: 'Poppins';
	font-weight: 500 !important;
}

@media (max-width: 576px) {
	.container {
		transform: translateY(5vh);
	}

